.form_type_login {
  width: 100%;
  margin: 0;
  padding-top: 100px;
  box-sizing: border-box;
  border-radius: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: none;
  box-shadow: none;
}

@media (max-width: 767px) {
  .form_type_login {
    padding-bottom: 10px;
  }
}
