.menu {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  visibility: hidden;
  opacity: 0;
  justify-content: end;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  transition: visibility 0.3s, opacity 0.3s linear;
  /*transition: right 0.3s;*/
}